import { ASTNode } from 'graphql'
import log from 'loglevel'

import { Api } from './Api'

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export type MockResponderMap = Map<ASTNode, <T>(...args: any[]) => T>

const nextRequestNumber = (() => {
  let requestNumber = 1
  return () => String(requestNumber++)
})()

export class MockApi implements Api {
  responseDelay = 0
  responders: MockResponderMap = new Map()

  constructor(responders?: MockResponderMap) {
    if (responders) this.setResponders(responders)
  }

  setResponders = (responders: MockResponderMap): void => {
    this.responders = responders
  }

  setResponseDelay = (ms: number): void => {
    this.responseDelay = ms
  }

  wait = async (): Promise<void> => {
    return new Promise((resolve) => {
      window.setTimeout(resolve, this.responseDelay)
    })
  }

  signOut = (): void => {
    log.debug('MockApi signOut')
  }

  query = async <T>(query: ASTNode, variables?: { [key: string]: unknown }): Promise<T> => {
    const requestNumber = nextRequestNumber()
    log.debug(`%cMockApi query #${requestNumber}`, 'background: lightgreen', query.loc?.source.body, variables)
    const responder = this.responders.get(query)
    await this.wait()
    if (responder) {
      const response = responder<T>(variables)
      log.debug(`%cMockApi responded to query #${requestNumber}`, 'background: green', response)
      return response
    }
    return new Promise(() => {
      log.debug(`%cMockApi not responding to query #${requestNumber}`, 'background: lightgray')
    })
  }

  queryAllPages = async <T>(
    query: ASTNode,
    variables: { [key: string]: unknown },
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    { dataProperty }: { dataProperty: string }
  ): Promise<T> => {
    const requestNumber = nextRequestNumber()
    log.debug(`%cMockApi queryAllPages #${requestNumber}`, 'background: lightgreen', query.loc?.source.body, variables)
    const responder = this.responders.get(query)
    await this.wait()
    if (responder) {
      const response = responder<T>(variables)
      log.debug(`%cMockApi responded to queryAllPages #${requestNumber}`, 'background: green', response)
      return response
    }
    return new Promise(() => {
      log.debug(`%cMockApi not responding to queryAllPages #${requestNumber}`, 'background: lightgray')
    })
  }

  mutation = async <T>(query: ASTNode, variables?: { [key: string]: unknown }): Promise<T> => {
    const requestNumber = nextRequestNumber()
    log.debug(`%cMockApi mutation #${requestNumber}`, 'background: #ffd17a', query.loc?.source.body, variables)
    const responder = this.responders.get(query)
    await this.wait()
    if (responder) {
      const response = responder<T>(variables)
      log.debug(`%cMockApi responded to mutation #${requestNumber}`, 'background: orange', response)
      return response
    }
    return new Promise(() => {
      log.debug(`%cMockApi not responding to mutation #${requestNumber}`, 'background: lightgray')
    })
  }
}
