import React, { Suspense } from 'react'
import { useRecoilValue } from 'recoil'

import { selectMyUser, selectBoxById } from '../../data/selectors'
import { BoxDetail as Component } from './BoxDetail'
import { Fallback } from './Fallback'

interface BoxDetailProps {
  boxId: string | number
}

const Connector = ({ boxId }: BoxDetailProps): JSX.Element => {
  const box = useRecoilValue(selectBoxById(Number(boxId)))
  // const user = useRecoilValue(selectMyUser)
  // const boxStates = useRecoilValue(selectBoxStates)

  if (box === null) return <h2>Box {boxId} not found.</h2>

  return (
    <Component
      box={box}
      //  user={user}
    />
  )
}

export const BoxDetail = (props: BoxDetailProps): JSX.Element => {
  return (
    <Suspense fallback={<Fallback />}>
      <Connector {...props} />
    </Suspense>
  )
}
