/** @jsxImportSource @emotion/react */
import React, { useEffect, useMemo } from 'react'
import { NAMCResource } from '@namcbugdb/common'
import { round } from 'lodash'
// import log from 'loglevel'

import { useApi } from '../../data/api'
import { useNotifications } from '../AppNotifications'
import { User, Sample, MyUser } from '../../types'
import { SampleInfoPanel } from '../SampleInfoPanel'
import { SelectField, SelectFieldProps } from '../SelectField'
import { NumberField } from '../NumberField'
import { CheckboxField } from '../CheckboxField'
import { ConfirmNavigateAway } from '../AppRouter'
import { ToggleEditModeButton } from './ToggleEditModeButton'
import { useUpdatableState } from '../../lib/useUpdatableState'
import { updateLabSort, startSort, stopSort } from '../../data/calls'
import MuiButton from '@mui/material/Button'
import { getTodayDate } from '../../lib/getTodayDate'

const LAB_SPLIT_PERCENT_PRECISION = 3

export interface SampleSortingProps {
  sorterUsers: User[]
  sample: Sample
  user: MyUser
}

interface Fields {
  sorterId: number | ''
  labSplitPercent: number | null
  sorterCount: number | null
  sortTime: number | null
  elutriation: boolean
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

export type ControllerState = {
  sample?: Sample
  values: Fields
  errors: FieldErrors
  editMode: 'stopped' | 'starting' | 'started' | 'stopping'
  persistence?: 'unsaved' | 'dirty' | 'saving' | 'saved' | 'error'
}

const initialState = {
  values: { sorterId: '', labSplitPercent: null, sorterCount: null, sortTime: null, sortDate: '', elutriation: false },
  editMode: 'stopped',
  persistence: 'unsaved',
  errors: undefined,
} as ControllerState

const getFieldErrors = (values: Fields): FieldErrors => {
  const errors = {
    sorterId: !values.sorterId,
    labSplitPercent: values.labSplitPercent === null,
    sorterCount: values.sorterCount === null,
    sortTime: values.sortTime === null,
  }
  if (!Object.values(errors).some((value) => value)) return undefined
  return errors
}
export interface ControllerActions {
  reset: () => void
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  setFieldValue: (fieldName: keyof Fields, value: any) => void
  startSort: () => void
  stopSort: () => void
  saveSort: () => void
}

const useComponentController = ({
  sample,
  // setSample,
  user,
  sorterUsers,
}: SampleSortingProps): [ControllerState, ControllerActions] => {
  const api = useApi()
  const notifcations = useNotifications()

  const [state, setState, updateState] = useUpdatableState<ControllerState>(initialState)

  const actions = {
    reset: () => {
      const { sorterId, sorterCount, sortTime, labSplit, elutriation } = sample
      const values: Fields = {
        sorterId: sorterId || '',
        sorterCount,
        sortTime,
        labSplitPercent: labSplit && round(labSplit * 100, LAB_SPLIT_PERCENT_PRECISION),
        elutriation: !!elutriation,
      }

      setState(() => ({
        ...initialState,
        sample,
        userId: user.id,
        values,
        persistence: getFieldErrors(values) ? 'unsaved' : 'saved',
        editMode: sample.sorterId === user.id && !sample.sortEndDate ? 'started' : 'stopped',
      }))
    },
    setFieldValue: async (fieldName: keyof Fields, value: unknown) => {
      if (!state.sample) throw new Error('No sample')
      updateState((draft) => {
        Object.assign(draft.values, { [fieldName]: value })
        draft.persistence = 'dirty'
      })
    },
    saveSort: async () => {
      try {
        const { sorterId, labSplitPercent, sorterCount, sortTime, elutriation } = state.values
        if (
          !state.sample?.id ||
          sorterId === '' ||
          labSplitPercent === null ||
          sorterCount === null ||
          sortTime === null
        )
          throw new Error('Missing values.')
        const updatedSample = {
          sampleId: state.sample.id,
          sorterId,
          labSplit: labSplitPercent / 100,
          sorterCount,
          sortTime,
          elutriation,
        }
        updateState((draft) => {
          draft.persistence = 'saving'
        })
        const success = await updateLabSort(api)(updatedSample)
        if (!success) throw new Error()
        const sorterName =
          sorterUsers.find(({ id }) => id === updatedSample.sorterId)?.fullName ?? state.sample.sorterName
        updateState((draft) => {
          draft.persistence = 'saved'
          Object.assign(draft.sample, { ...updatedSample, sorterName })
        })
      } catch (err) {
        notifcations.create({ message: `Failed to update Lab Sort: ${err}`, type: 'error' })
        updateState((draft) => {
          draft.persistence = 'error'
        })
      }
    },
    startSort: async () => {
      if (!state.sample) throw new Error('No sample')
      try {
        updateState((draft) => {
          draft.editMode = 'starting'
        })
        const success = await startSort(api)({ sampleId: state.sample.id })
        if (!success) throw new Error()
        updateState((draft) => {
          draft.editMode = 'started'
          draft.values.sorterId = user.id
          draft.sample.sorterId = user.id
          draft.sample.sorterName = user.fullName
        })
      } catch (err) {
        notifcations.create({ message: `Failed to start Lab Sort: ${err}`, type: 'error' })
        updateState((draft) => {
          draft.editMode = 'stopped'
        })
      }
    },
    stopSort: async () => {
      if (!state.sample) throw new Error('No sample')
      try {
        updateState((draft) => {
          draft.editMode = 'stopping'
        })
        const success = await stopSort(api)({ sampleId: state.sample.id })
        if (!success) throw new Error()
        const todayDate = getTodayDate()
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.sample.sortEndDate = todayDate
        })
      } catch (err) {
        notifcations.create({ message: `Failed to stop Lab Sort: ${err}`, type: 'error' })
        updateState((draft) => {
          draft.editMode = 'started'
        })
      }
    },
  }

  const errors = state.editMode === 'stopped' ? undefined : getFieldErrors(state.values)

  return [{ ...state, errors }, actions]
}

export const SampleSorting = (props: SampleSortingProps): JSX.Element => {
  const { sorterUsers: sorterUsersProp, sample, user } = props

  // STATE

  const sorterUsers = useMemo(() => {
    const resultSorterUsers = [...sorterUsersProp]
    if (!sorterUsersProp.find((sorterUser) => sorterUser.id === user.id)) resultSorterUsers.push(user)
    return resultSorterUsers
  }, [sorterUsersProp])

  const [state, actions] = useComponentController({ sample, user, sorterUsers })
  const {
    values: { sorterId, labSplitPercent, sorterCount, sortTime, elutriation },
    errors,
    editMode,
  } = state
  const { setFieldValue } = actions
  const isEditing = editMode === 'started'

  // EFFECTS

  useEffect(() => {
    actions.reset()
  }, [sample.id])

  // RENDER

  const sorterOptions: SelectFieldProps['options'] = sorterUsers.map((sorterUser) => ({
    value: sorterUser.id,
    label: sorterUser.fullName,
  }))
  if (sample.sorterId && !sorterUsers.find((sorterUser) => sorterUser.id === sorterId))
    sorterOptions.push({
      value: sorterId,
      label: sample.sorterName,
    })

  const canSort = !!user.permissions.updateOwn?.includes(NAMCResource.sorting)
  const isLabManager = !!user.permissions.updateAny?.includes(NAMCResource.sorting)
  const canStopOwn = state.editMode === 'started' && state.persistence === 'saved'
  const canStopOther = isLabManager
  const canSave = state.persistence === 'dirty' && !errors

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        {state.sample && (
          <SampleInfoPanel
            sample={state.sample}
            actions={
              canSort && (
                <ToggleEditModeButton
                  userId={user.id}
                  sample={state.sample}
                  actions={actions}
                  canStopOwn={canStopOwn}
                  canStopOther={canStopOther}
                  editMode={state.editMode}
                />
              )
            }
          />
        )}
        <div css={{ display: 'grid', gridTemplateColumns: '20em', gap: '1em' }}>
          <SelectField
            label="Sorter"
            value={sorterId}
            onChange={(value) => setFieldValue('sorterId', value)}
            options={sorterOptions}
            error={errors?.sorterId}
            readOnly={!isEditing}
            disabled={!isLabManager}
          />
          <NumberField
            label="Split %"
            value={labSplitPercent}
            onChange={(value) => setFieldValue('labSplitPercent', value)}
            min={0}
            max={100}
            precision={LAB_SPLIT_PERCENT_PRECISION}
            error={errors?.labSplitPercent}
            readOnly={!isEditing}
          />
          <NumberField
            label="Number of bugs"
            value={sorterCount}
            onChange={(value) => setFieldValue('sorterCount', value)}
            min={0}
            max={999999}
            error={errors?.sorterCount}
            readOnly={!isEditing}
          />
          <CheckboxField
            label="Elutriation"
            value={elutriation}
            onChange={(value) => setFieldValue('elutriation', value)}
            readOnly={!isEditing}
          />
          <NumberField
            label="Sort time (hours)"
            value={sortTime}
            onChange={(value) => setFieldValue('sortTime', value)}
            min={0}
            max={999999}
            error={errors?.sortTime}
            readOnly={!isEditing}
          />
          {(editMode === 'started' || editMode === 'stopping') && (
            <MuiButton onClick={actions.saveSort} disabled={!canSave} variant="contained">
              Save
            </MuiButton>
          )}
        </div>
      </div>
      {editMode !== 'stopped' && (
        <ConfirmNavigateAway message="Sorting is still in progress. Are you sure you want to leave?" />
      )}
    </>
  )
}
